import { useState } from "react";
import ReactDOM from "react-dom/client";

import znaika from './pics/characters/.jpg';
import vintik from './pics/characters/.jpg';
import pilulkin from './pics/characters/.jpg';
import ponchik from './pics/characters/.jpg';
import guslia from './pics/characters/.jpg';
import sineglazka from './pics/characters/.jpg'
import neznaika from './pics/characters/.jpg';
import gaspar from './pics/sliders/laboratory.jpg'
import kitchen from './pics/sliders/kitchen.jpg'
import square from './pics/sliders/square.jpg'
import square2 from './pics/sliders/square2.jpg'
import garden from './pics/sliders/garden.jpg';
import prison from './pics/sliders/prison.jpg';
import heir from './pics/sliders/heir.jpg';
import palace from './pics/sliders/palace.jpg';
import stadium from './pics/madness/stadium.jpg';
import dinosaur from './pics/madness/dinosaur.jpg';
import balloon from './pics/madness/balloon.jpg';
import god from './pics/madness/god.jpg';
import space from './pics/madness/space.jpg';
import towers from './pics/madness/towers.jpg';
import show from './pics/show.png'


function Characters(){
    let [disp,setDisp]=useState('none')
 function Switch(){
    if(disp==='none'){
        setDisp('block')
        
    }else if(disp==='block'){
        setDisp('none')
    }
 }

    return        <section class='character'>
    <header  class='header1' onClick={Switch}><div>CHARACTER POSTERS</div></header>
    <div class="body1" style={{display:disp}}>
        <div class="grid">
         <div class='text'>Special character posters, made for Circus show Adventures of Neznaika and his friends<p>They were made for introdusing these characters to children and their parents</p></div>
        <img src={znaika} alt=""/>
        <img src={vintik} alt=""/>
        <img src={pilulkin} alt=""/>
        <img src={ponchik} alt=""/>
        <img src={guslia} alt=""/>
        <img src={sineglazka} alt=""/>
        <img src={neznaika} alt=""/>
        </div>
    </div>

      </section>
}

function Decoration(){
    let [disp,setDisp]=useState('none')
    function Switch(){
       if(disp==='none'){
           setDisp('block')
       }else if(disp==='block'){
           setDisp('none')
       }
    }
    return <section class='decoration'>
<header class='header2' onClick={Switch}><div>SHOW DECORATION</div></header>
<div class='body1 body2 'style={{display:disp}}>
    <div class="grid3">
        <div class='text'><p>Special sliders for Circus show, used on background screen during it</p></div>
        <img src={gaspar} alt ='Gaspar'/>
        <img src={kitchen} alt='Kitchen'/>
        <img src={square} alt='Square'/>
        <img src={square2} alt='Square2'/>
        <img src={garden} alt='Garden'/>
        <img src={prison} alt='Prison'/>
        <img src={heir} alt='Heir'/>
        <img src={palace} alt='Palace'/>
    </div>
</div>
    </section>
}
function Madness(){
    let [disp,setDisp]=useState('none')
    function Switch(){
       if(disp==='none'){
           setDisp('block')
       }else if(disp==='block'){
           setDisp('none')
       }
    }

    return <section class='madness'>
        <header class='header3' onClick={Switch}><div>SOCIAL NETWORK MADNESS</div></header>
        <div class='body1 body3' style={{display:disp}}>
            <div class='grid3'>
               <img  src={stadium} alt ="Stadium"/>
               <img src={dinosaur} alt ="Dinosaur"/>
               <img  src={balloon} alt ="Balloon"/>
               <img  src={god} alt ="God"/>
               <img src={space} alt ="Space"/>
               <img src={towers} alt ="Towers"/>

            </div>
            
            </div>
    </section>
}
function Poster(){
    let [disp,setDisp]=useState('none')
    function Switch(){
       if(disp==='none'){
           setDisp('block')
       }else if(disp==='block'){
           setDisp('none')
       }
    }
    let [w,setW]=useState('100%')
    
    function Width(){
        let img=document.querySelector('.img-poster');

     if(w==='100%'){setW('40%');
     img.style.height='10px;'
     img.style.marginLeft='30%';
     
    }
     else if(w==='40%'){setW('100%')
     img.style.height='100%'
     img.style.marginLeft='0%';
   
    }
     
    }
   
    return <section  class='poster'>
        <header class='header4' onClick={Switch}><div >ILLUSION SHOW POSTER</div></header>
        <div class='body1 body4' id="poster" style={{display:disp}}>
            <img class='img-poster' style={{width:w}} onClick={Width} src={show} alt='show'/>
        </div>
    </section>
}

function Circus(){
    return <div id='circus'>
<Characters/>
        <Decoration/>
        <Madness/>
        <Poster/>
             </div>
}
export default Circus