import { useState } from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Mousewheel,Pagination} from "swiper";
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

import stock1 from './pics/NewStocks/stock1.jpg';
import stock2 from './pics/NewStocks/stock2.jpg';
import stock3 from './pics/NewStocks/stock3.jpg';
import stock4 from './pics/NewStocks/stock4.jpg';
import stock5 from './pics/NewStocks/stock5.jpg';
import stock6 from './pics/NewStocks/stock6.jpg';
import stock7 from './pics/NewStocks/stock7.jpg';
import stock8 from './pics/NewStocks/stock8.jpg';

import target1 from './pics/Target/target1.jpg'
import target2 from './pics/Target/target2.jpg'
import target3 from './pics/Target/target3.jpg'
import target4 from './pics/Target/target4.jpg'
import target5 from './pics/Target/target5.jpg'
import target6 from './pics/Target/target6.jpg'
import target7 from './pics/Target/target7.jpg'
import target8 from './pics/Target/target8.jpg'

import insta1 from './pics/Insta/insta1.jpg'
import insta2 from './pics/Insta/insta2.jpg'
import insta3 from './pics/Insta/insta3.jpg'
import insta4 from './pics/Insta/insta4.jpg'
import insta5 from './pics/Insta/insta5.jpg'
import insta6 from './pics/Insta/insta6.jpg'
import insta7 from './pics/Insta/insta7.jpg'
import insta8 from './pics/Insta/insta8.jpg'

import catalogue1 from './pics/Catalogue/catalogue1.jpg'
import catalogue2 from './pics/Catalogue/catalogue2.jpg'
import catalogue3 from './pics/Catalogue/catalogue3.jpg'
import catalogue4 from './pics/Catalogue/catalogue4.jpg'
import catalogue5 from './pics/Catalogue/catalogue5.jpg'
import catalogue6 from './pics/Catalogue/catalogue6.jpg'

let numberStock=4
if(window.innerWidth<=450){
  numberStock=1
}else if(window.innerWidth<=750){
numberStock=1.5
}

let numberTarget=5.3
if(window.innerWidth<=450){
  numberTarget=1
}else if(window.innerWidth<=750){
numberTarget=1.2
}

let numberCatalog=2
if(window.innerWidth<=700){
  numberCatalog=1
}
function Stock(props){
 
    let [slides,setSlides]=useState(props.num)
    let [value1,setValue1]=useState('')
   
    let [Hh,setHh]=useState('0px')

    const one=()=>{setValue1(props.img1)}
    const two=()=>{setValue1(props.img2)}
    const three=()=>{setValue1(props.img3)}
    const four=()=>{setValue1(props.img4)}
    const five=()=>{setValue1(props.img5)}
    const six=()=>{setValue1(props.img6)}
    const seven=()=>{setValue1(props.img7)}
    const eight=()=>{setValue1(props.img8)}


    function Expand(){
      if(window.innerWidth>=7500){
setHh(document.querySelector('.img-MA').offsetHeight+'px');
setSlides(8);}
    }
    function Collapse(){
        setHh('0px');
        setSlides(props.num);
            }
return <div >
    <header >

<pre>{props.title}</pre>

<div className='line'></div>
    </header>
  <div className='anime-MA' style={{height:Hh}} >
    <img class="img-MA" src={value1} onClick={Collapse} />
  
  </div>
        
    
    <Swiper
    id="swiper"
    onClick={Expand}
        spaceBetween={0}
        slidesPerView={slides}
        
        onSlideChange={() => console.log('slide change')}
        onSwiper={(swiper) => console.log(swiper)}
        direction="horizontal"
       
          pagination={{
            clickable: true,
          }}
         mousewheel={true}
         
          modules={[Autoplay,Mousewheel,Pagination]}
       
        
        loop={true}>
          <SwiperSlide className='swiper' onClick={one} ><img className="img1" src={props.img1}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={two}><img className='img2'src={props.img2}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={three}><img className='img3'  src={props.img3}/></SwiperSlide>
          <SwiperSlide className='swiper'  onClick={four}><img className='img4'  src={props.img4}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={five}><img className='img5' src={props.img5}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={six}><img className='img6' src={props.img6}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={seven}><img className='img7' src={props.img7}/></SwiperSlide>
          <SwiperSlide className='swiper' onClick={eight}><img className='img8' src={props.img8}/></SwiperSlide>

      </Swiper>
</div>
}



function Catalogue(props){
  
  let [slides,setSlides]=useState(props.num)
  let [value1,setValue1]=useState('')
 
  let [Hh,setHh]=useState('0px')

  const one=()=>{setValue1(props.img1)}
  const two=()=>{setValue1(props.img2)}
  const three=()=>{setValue1(props.img3)}
  const four=()=>{setValue1(props.img4)}
  const five=()=>{setValue1(props.img5)}
  const six=()=>{setValue1(props.img6)}

  function Expand(){
    if(window.innerWidth>=700){
setHh(document.querySelector('.img-MA').offsetHeight+'px');
setSlides(6);}
  }

 
  function Collapse(){
      setHh('0px');
      setSlides(props.num);
          }
return <div >
  <header >

<pre>{props.title}</pre>

<div className='line'></div>
  </header>
<div className='anime-MA' style={{height:Hh}} >
  <img class="img-MA" src={value1} onClick={Collapse} />
 
</div>
      
  
  <Swiper
  onClick={Expand}
      spaceBetween={0}
      slidesPerView={slides}
      
      onSlideChange={() => console.log('slide change')}
      onSwiper={(swiper) => console.log(swiper)}
      direction="horizontal"
     
        pagination={{
          clickable: true,
         bulletClass: `swiper-pagination-bullet swiper-pagination-testClass`
        }}
       mousewheel={true}
        modules={[Autoplay,Mousewheel]}
     
      
      loop={true}>
        <SwiperSlide className='swiper' onClick={one} ><img src={props.img1}/></SwiperSlide>
        <SwiperSlide className='swiper' onClick={two}><img src={props.img2}/></SwiperSlide>
        <SwiperSlide className='swiper' onClick={three}><img src={props.img3}/></SwiperSlide>
        <SwiperSlide className='swiper'  onClick={four}><img  src={props.img4}/></SwiperSlide>
        <SwiperSlide className='swiper' onClick={five}><img src={props.img5}/></SwiperSlide>
        <SwiperSlide className='swiper' onClick={six}><img  src={props.img6}/></SwiperSlide>
       

    </Swiper>
</div>
}



function MarcAndre(){
    return <section id='MA'>
     <Stock
     num={numberStock}
     title={'NEW STOCK'}
      img1={stock1}
        img2={stock2}
         img3={stock3}
          img4={stock4}
           img5={stock5}
            img6={stock6}
             img7={stock7}
              img8={stock8}/>   

<Stock
num={numberTarget}
     title={'TARGET'}
      img1={target1}
        img2={target2}
         img3={target3}
          img4={target4}
           img5={target5}
            img6={target6}
             img7={target7}
              img8={target8}/>    
       <Stock
       num={numberTarget}
     title={'INSTA'}
      img1={insta1}
        img2={insta2}
         img3={insta3}
          img4={insta4}
           img5={insta5}
            img6={insta6}
             img7={insta7}
              img8={insta8}/>               
         <Stock
         num={numberCatalog}
     title={'CATALOGUE LAYOUT'}
      img1={catalogue1}
        img2={catalogue2}
         img3={catalogue3}
          img4={catalogue4}
           img5={catalogue5}
            img6={catalogue1}
            img7={catalogue3}
            img8={catalogue4}
            />
    
    </section>
}
export default MarcAndre