import { useState } from "react";
import {HashRouter, Link, Route, Switch } from 'react-router-dom';

if(window.innerWidth>650){
  setInterval(Url,1);
}


function Url(){
  let wi=window.location.href;
  var res = wi.charAt(wi.length-1);
  if(res==='/'){
  document.querySelectorAll('.link')[0].style.color='red';
  document.querySelectorAll('.link')[1].style.color='white';
  document.querySelectorAll('.link')[2].style.color='white';
  document.querySelectorAll('.link')[3].style.color='white';
  
  }
  else if(res==='k'){
    document.querySelectorAll('.link')[0].style.color='white';
  document.querySelectorAll('.link')[1].style.color='white';
  document.querySelectorAll('.link')[2].style.color='red';
  document.querySelectorAll('.link')[3].style.color='white';
  }
  else if(res==='t'){
    document.querySelectorAll('.link')[0].style.color='white'
  document.querySelectorAll('.link')[1].style.color='red';
  document.querySelectorAll('.link')[2].style.color='white'
  document.querySelectorAll('.link')[3].style.color='white'
  }
  else if(res==='g'){
    document.querySelectorAll('.link')[0].style.color='white'
  document.querySelectorAll('.link')[1].style.color='white';
  document.querySelectorAll('.link')[2].style.color='white'
  document.querySelectorAll('.link')[3].style.color='red'
  
  }
 else{
  document.querySelectorAll('.link')[0].style.color='white'
  document.querySelectorAll('.link')[1].style.color='white';
  document.querySelectorAll('.link')[2].style.color='white'
  document.querySelectorAll('.link')[3].style.color='white';

 }
  }
  
function RightComp(){
 


    return <div >
    <div class="right"onClick={Url} onLoad={Url} > 
                       <pre> <Link class=' link'     to='./'><span class='red'>Home  </span></Link><span>|</span>
                        <Link class='link' id='about'  to='./about'> <span class='red'>  About  </span> </Link><span>|</span>
                        <Link class='link'  to='./work'><span class='red'>   Work  </span> </Link><span>|</span>
                        <Link class='link'  to='./pricing'><span class='red'>  Pricing  </span></Link></pre></div>
    </div>
    
    }
    
    
    function RightMobile(){
      const [sw, setSw] = useState("none");
      const [fa,setFa]=useState('fa fa-bars')
    let display=()=>{
    if(sw=='none')  
      {setSw('flex');
    setFa('fa fa-close')
    }
      else {
        setSw('none');
        setFa('fa fa-bars')
      }
    
    }
      return (
        <div onClick={display}>
        <i class={fa}  ></i>
    <div style={{display:sw}} class="rightM" > 
    
                        <Link   to='./'><div>Home</div> </Link>
                        <Link   to='./about'><div> About </div> </Link>
                        <Link   to='./work'><div> Work </div></Link>
                        <Link   to='./pricing'><div>Pricing</div></Link> 
                        </div>
      </div>
      )
    }
   
    function Right(){
      let w=window.innerWidth;
      if(w>650){
        return <div><RightComp/></div>
      }
      return<div><RightMobile/></div>
    }
    export default Right;